import * as React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";

import Image from "./img/cat_kotatsu_neko.png";

import Countbutton from "./countbutton";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 345 }}>
      <CardMedia sx={{ height: 200 }} image={Image} title="green iguana" />
      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          日向夏ドリンク
        </Typography>
        <Typography variant="body2" color="text.secondary">
          日向夏は1820年ごろ､宮崎市で偶発実生として発見され､1887年(明治20年)に日向夏と命名今では宮崎特産の果物となっています｡独特の風味からユズの血を引いているのではないかと言われています。
          酸味が高いため､低果汁での商品化を図り､フレーバーと適度な苦味･酸味を調和させ、すっきりと爽やかなドリンクに仕上げました｡他の柑橘では味わえない独特な芳香と爽やかな酸味が特徴です。
        </Typography>
      </CardContent>
      <Countbutton />
    </Card>
  );
}
